import * as React from 'react';
import { styled } from '@mui/material/styles';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import CardActions from '@mui/material/CardActions';
import Collapse from '@mui/material/Collapse';
import IconButton, { IconButtonProps } from '@mui/material/IconButton';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import { Col, Row } from 'react-bootstrap';
import SelectInput from 'components/input/SelectInput';
import TextInput from 'components/input/textInput';
import CheckboxInput from 'components/input/CheckboxInput';

interface ExpandMoreProps extends IconButtonProps {
  expand: boolean;
}

const ExpandMore = styled((props: ExpandMoreProps) => {
  const { expand, ...other } = props;
  return <IconButton {...other} />;
})(({ theme, expand }) => ({
  transform: !expand ? 'rotate(0deg)' : 'rotate(180deg)',
  marginLeft: 'auto',
  transition: theme.transitions.create('transform', {
    duration: theme.transitions.duration.shortest,
  }),
}));
interface Props {
  language?: string;
  skillCount?: number;
}

const LanguageConfig = (props: Props) => {
  const [expanded, setExpanded] = React.useState(false);

  const handleExpandClick = () => {
    setExpanded(!expanded);
  };
  return (
    <Card className="w-100 my-3">
      <CardActions
        disableSpacing
        onClick={handleExpandClick}
        sx={{
          backgroundColor: expanded ? '#F5F1FF' : 'none',
          borderBottom: expanded ? '2px solid' : 'none',
          borderBottomColor: expanded ? '#5F30E2' : 'none',
        }}
      >
        <div className="w-100 d-flex justify-content-between">
          <span className="p-1">{props.language}</span>
          <strong className="text-muted">{props.skillCount}/3</strong>
        </div>
        <ExpandMore
          expand={expanded}
          onClick={handleExpandClick}
          aria-expanded={expanded}
          aria-label="show more"
        >
          <ExpandMoreIcon />
        </ExpandMore>
      </CardActions>
      <Collapse in={expanded} timeout="auto" unmountOnExit>
        <CardContent>
          <Row>
            <SelectInput
              option={[{ value: props.language }]}
              mdCol={'6'}
              label={'Language'}
              name={'language'}
              // values={props.formik.values.language}
              // onChange={props.formik.handleChange}
              // feedback={
              //   props.formik.touched.language &&
              //   props.formik.errors.language && (
              //     <span className="text-danger">{props.formik.errors.language}</span>
              //   )
              // }
            />
            <TextInput
              mdCol={'6'}
              label={'Fluency'}
              name={'fluency'}
              values={undefined}
              onChange={() => {}}
            />
            <div className="text-muted">Select your Language Proficiency</div>
            <Col md={3}>
              <CheckboxInput
                label="First Language"
                name=""
                values={undefined}
                onChange={() => {}}
              />
            </Col>
            <Col md={2}>
              <CheckboxInput label="Speak" name="" values={undefined} onChange={() => {}} />
            </Col>
            <Col md={2}>
              <CheckboxInput label="Read" name="" values={undefined} onChange={() => {}} />
            </Col>
            <Col md={2}>
              <CheckboxInput label="Write" name="" values={undefined} onChange={() => {}} />
            </Col>
            <Col md={3}>
              <CheckboxInput
                label="Spoken at Home"
                name=""
                values={undefined}
                onChange={() => {}}
              />
            </Col>
          </Row>
        </CardContent>
      </Collapse>
    </Card>
  );
};

export default LanguageConfig;
